import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import AuthContext from '../../../../../context/auth/authContext'
import logo from "./../../../../../assets/img/logo.svg"
import searchicon from "./../../../../../assets/img/search.svg";


const PosHeader = () => {

  const { usu_nom, cerrarSesion } = useContext(AuthContext)

    return (
        <header className="header">
        <div className="header__logo">
          <img src={logo} alt="" />
        </div>
        <div className="header__buscador">
          <img src={searchicon} alt="" />
          <input type="text" className="header__input" placeholder="Busca un término" />
        </div>
        <div className="header__usuario mr-5">

          <Link className="btn btn-success mr-2" to="/admin/dashboard">Ir a Admin</Link>
          <button className="btn btn-danger mr-2"><i class="fas fa-door-open    " onClick={()=>{
            cerrarSesion()
          }}>Salir</i></button>
          <img src="https://randomuser.me/api/portraits/men/90.jpg" alt="" className="mr-2"/>
          <span>{usu_nom}</span>
        </div>
      </header>
    )
}

export default PosHeader
