import React, { useEffect, useState } from 'react'
import { postVerificarToken } from '../../services/authService';
import AuthContext from './authContext'
import { useHistory } from "react-router-dom"

const AuthState = ({children}) => {


    const history = useHistory()
    
    const [auth, setAuth] = useState({
        autenticado: false,
        usu_nom: null,
        usu_id: null,
        token: null,
        cargando: true,
    });
    
    const cerrarSesion = () => {
        localStorage.removeItem("token")
        //PUSH() VS REPLACE()
        //PUSH() NOS DA LA POSIBILIDAD DE NAVEGAR A UNA NUEVA RUTA, SIN EMBARGO 
        //PODEMOS CLICAR EN EL BOTON ATRÁS DEL NAVEGADOR Y PODEMOS TENER PROBLEMAS.
        //REPLACE() MISMO COMPORTAMIENTO PEO BORRA EL HISTORIAL DE NAVEGACIÓN 
        //EN NUESTRO PROYECTO
        history.replace("/auth/login")
        setAuth({
            autenticado: false,
            usu_nom: null,
            usu_id: null,
            token: null,
            cargando: true,
        })
    }

    const iniciarSesionContext = (token= "") => {
        
        //GUARDAMOS EL TOKEN EN EL LOCALSTORAGE PORQUE CUANDO
        //ACTUALIZAMOS LA PÁGINA, EL LOCALSTORAGE NO SE BORRA
        localStorage.setItem("token", token)


        const payload = token.split(".")[1];
        const payloadDesecriptado = window.atob(payload)
        console.log(payloadDesecriptado)
        const payloadJSON = JSON.parse(payloadDesecriptado)
        console.log(payloadJSON)
        setAuth({
            autenticado:true,
            usu_nom: payloadJSON.usu_nom,
            usu_id: payloadJSON.usu_id,
            token: token,
            cargando: false,
        })
    }


    const iniciarSesionConLocalStorage = ()=> {
        const token = localStorage.getItem("token")
        if(token){
            ///VERIFICAR SI EL TOKEN TIENE TIEMPO DE VIDA 
            // A TRAVES DEL BACKEND
            postVerificarToken(token).then(data => {
            console.log(data);
            if(data.ok){
                const payload = token.split(".")[1];
                const payloadDesecriptado = window.atob(payload)
                console.log(payloadDesecriptado)
                const payloadJSON = JSON.parse(payloadDesecriptado)
                console.log(payloadJSON)
                setAuth({
                    autenticado:true,
                    usu_nom: payloadJSON.usu_nom,
                    usu_id: payloadJSON.usu_id,
                    token: token,
                    cargando: false,
                })
            } else{
                //SI HABIA TOKEN, PERO HABÍA CADUCADO O EL TOKEN ESTABA ALTERADO
                localStorage.removeItem("token");
                setAuth({
                    autenticado:false,
                    usu_nom: null,
                    usu_id: null,
                    token: null,
                    cargando: false,
                })
            }   
            //Si el token es válido, iniciar sesión en el state del context
            })
        }else {
            //NO HACER NADA O REDIRECCIONAR AL HOME

            setAuth({
                autenticado: false,
                usu_nom: null,
                usu_id: null,
                token: null,
                cargando: false
            })
        }
    }
    useEffect(()=>{
        iniciarSesionConLocalStorage()
    }, [])
    return (
        <AuthContext.Provider value={{
            autenticado: auth.autenticado,
            usu_nom: auth.usu_nom,
            usu_id:auth.usu_id,
            token: auth.token,
            cargando: auth.cargando,
            iniciarSesionContext: iniciarSesionContext,
            cerrarSesion: cerrarSesion,
        }}>
            {children}
        </AuthContext.Provider>
    )
}

export default AuthState
