import React, { useContext, useState } from "react";
import AuthContext from "../../../../context/auth/authContext";
import { postLogin } from "../../../../services/authService";



const AuthLoginScreen = (props) => {

    const { iniciarSesionContext } = useContext(AuthContext)
    const [formulario, setFormulario] = useState({
        correo: "",
        password: ""
    })
    const handleChange = e => {
        setFormulario({
            ...formulario,
            [e.target.name]: e.target.value
        })
    }
    const submit = e => {
        e.preventDefault();
        postLogin(formulario).then(rpta => {
            console.log(rpta)
            if(rpta.ok) {
                ///SIGNIFICA LOGIN EXITOSO
                iniciarSesionContext(rpta.token)
                props.history.push("/pos/pedido")
            }
        })
    }

  return (
    <>
      <main className="container-fluid bg-dark login-screen">
        <div className="row">
            {/* <div className="col-md-3"></div> */}
          <div className="col-md-4">
            <div className="card shadow">
              <div className="card-body">
                <form onSubmit={submit}>
                  <div className="form-group">
                    <label htmlFor="">Usuario</label>
                    <input
                    onChange={handleChange}
                    value={formulario.correo}
                      type="text"
                      placeholder="Ejm: Jcueva@gmail.com"
                      className="form-control"
                      name="correo"
                      id="correo"
                    />
                  </div>
                  <div className="form-group">
                    <label htmlFor="">Password</label>
                    <input
                    onChange={handleChange}
                    value={formulario.password}
                      type="password"
                      className="form-control"
                      name="password"
                      id="password"
                    />
                  </div>
                  <div className="form-group">
                  <button type="submit" className="btn btn-dark">INGRESAR</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
          {/* <div className="col-md-3"></div> */}
  
        </div>
      </main>
    </>
  );
};

export default AuthLoginScreen;
