import React from 'react';
import { Switch, Route } from "react-router-dom";
import AuthLoginScreen from './Screens{/login/AuthLoginScreen';
import AuthRegisterScreens from './Screens{/register/AuthRegisterScreens';

const AuthRouter = () => {
    return (
        <>
          <Switch>
             <Route path="/auth/login" component={AuthLoginScreen} /> 
             <Route path="/auth/register" component={AuthRegisterScreens} /> 
          </Switch>
        </>
    )
}

export default AuthRouter
